<template>
	<div>
		<van-nav-bar :title=titles />
		<van-tabbar route>
			<van-tabbar-item to="/layout/home" icon="home-o">首页</van-tabbar-item>
			<van-tabbar-item to="/layout/search" icon="search">搜索</van-tabbar-item>
		</van-tabbar>
		<div class="main">
			<router-view></router-view>
		</div>
	</div>
</template>

<script>
	export default {
		data() {
			return {
				active: 0,
				titles: this.$route.meta.titles,
			};
		},

		watch: {
			// 当组件发生改变时  会监听到 将路由里得到meta 传给当前的titles
			$route() {
				this.titles = this.$route.meta.titles
			}
		}
	};
</script>

<style>
	.main {
		padding-top: 46px;
		padding-bottom: 50px;
	}

	.van-nav-bar {
		position: fixed;

		width: 100%;
	}
</style>
