import Vue from 'vue'
import VueRouter from 'vue-router'

import Layout from '@/views/Layout'
import Home from '@/views/Home'
import Play from '@/views/Play'
import Search from '@/views/Search'


Vue.use(VueRouter)

const routes = [{
		path: '/',
		redirect: '/layout'
	},
	{
		path: '/layout',
		component: Layout,
		redirect: '/layout/home',
		children: [{
				path: 'home',
				component: Home,
				meta: {
					titles: '首页'
				}
			},
			{
				path: 'search',
				component: Search,
				meta: {
					titles: '搜索'
				}
			},
			{
				path: '/play',
				component: Play
			}
		]
	}
]



export default new VueRouter({
	routes
})
