// 文件名-尽量和模块页面文件名统一(方便查找)
import request from '@/utils/request' //将基础接口引用过来

// 首页 - 推荐歌单
export const recommendMusic = params => request({ //利用params传参   把数据交给recommendMusic   并向外暴露
	url: '/personalized',
	params
	// 将来外面可能传入params的值 {limit: 20}
})
export const newMusic = params => request({
	url: "/personalized/newsong",
	params
})
