<template>
	<div>
		<p class="title">推荐歌单</p>
		<van-row gutter="8">
			<van-col span="8" v-for="obj in reList" :key="obj.id">
				<van-image width="100%" height="3rem" fit="cover" :src="obj.picUrl" />
				<p class="song_name">{{ obj.name }}</p>
			</van-col>
		</van-row>

		<p class="title">最新音乐</p>
		<SongItem v-for="obj in songList" :key="obj.id" :name="obj.name" :author="obj.song.artists[0].name"
			:id="obj.id"></SongItem>
	</div>
</template>




<script>
	import {
		recommendMusicAPI,
		newMusicAPI
	} from "@/api"; //将暴露出来的数据接口在本页面拿到
	import SongItem from '@/components/SongItem.vue'

	export default {
		components: {
			SongItem
		},
		data() {
			return {
				reList: [], // 推荐歌单数据
				songList: [], // 最新音乐数据
			};
		},
		async created() {
			const res = await recommendMusicAPI({ //从recommendMusicAPI拿到6条数据
				limit: 6,
			});
			console.log(res);
			this.reList = res.data.result;

			const res2 = await newMusicAPI({
				limit: 20
			})
			console.log(res2);
			this.songList = res2.data.result

		},
	};
</script>



<style scoped>
	.title {
		padding: 0 20px;
		background-color: pink;
		font-size: 24px;
		color: #fff;
		font-family: 仿宋;
	}

	.song_name {
		font-size: 0.346667rem;
		padding: 0 0.08rem;
		margin-bottom: 0.266667rem;
		word-break: break-all;
		text-overflow: ellipsis;
		display: -webkit-box;
		/** 对象作为伸缩盒子模型显示 **/
		-webkit-box-orient: vertical;
		/** 设置或检索伸缩盒对象的子元素的排列方式 **/
		-webkit-line-clamp: 2;
		/** 显示的行数 **/
		overflow: hidden;
		/** 隐藏超出的内容 **/
	}
</style>
